.App {
    /* text-align: center; */
    display: flex;
    /* flex-direction: column-reverse; */
    /* flex-direction: row; */
    /* align-items: center; */
    width: 100%;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.chart {
    width: 100%;
}

.StationWrap {
    max-height: 97vh;
    overflow-y: scroll;
    overflow-x: hidden;
}

.Station {
    background-color: beige;
    width: 400px;
    height: 60px;
    border: blanchedalmond 2px solid;
    display: flex;
    flex-direction: column;
    align-items: baseline;
    padding: 8px;
}

#Name {
    font-weight: 800;
}

.searchbar {
    height: 30px;
    width: 420px;
}

@media screen and (max-width: 600px) {
    .App {
        flex-direction: column-reverse;
        align-items: center;
    }
    .Station {
        width: 100%;
    }
    .searchbar {
        width: 95%;
    }
}